<template>
	<section>
		<div class="encyc-banner">
			
		</div>
		<div class="bk-ppbj"></div>
	<div class="bk-box">
		<div class="inner">
			<p class="bk-title"><span>热门文章</span></p>
			<ul class="bk-hot clearfix">
				<li class="bk-img" v-for=" (item,index) in topImg" v-if="index<5">
					<a href="javascript:;"  @click="kipPage(item.uuid)"  v-if="item.extend1 == '' ">
					<img src="../../assets/images/bk_pic2.jpg" :title="item.tex">
					</a>
					<a href="javascript:;" @click="kipPage(item.uuid)" v-else>
					<img :src='item.extend1+"?x-oss-process=image/resize,m_mfit,h_125,w_216,limit_0/resize,m_fill,h_125,w_216"'/>
					</a>
					<p>
				     <a @click="kipPage(item.uuid)">{{item.title | truncate(10)}}</a>
					</p>
				 </li>
			</ul>
			<p class="bk-title bk-title2"><span>石油百科分类信息</span></p>
			<div class="bk-cont clearfix">
				<div class="bk-fl-l">
					<div v-for="item in getClassify" class="bk-fl-list clearfix">
						<p class="curr" v-if="item.subList.length>0">{{item.channelMName}}</p>
						<ul v-for="val in item.subList" >
							<li v-if="val.channelName.length <=8"><a href="javascript:;" @click="clickgetClassifyConten(val.channelName)" >{{val.channelName}}</a></li>
							<li v-if="val.channelName.length >8"><a href="javascript:;" @click="clickgetClassifyConten(val.channelName)" :title="val.channelName">{{val.channelName | truncate(9)}}</a></li>
						</ul>
						</div>
				    </div>
				       
				<div class="bk-fl-r">
					<div class="bk-r-blo clearfix" v-for="item in classifyconten">  <!--:src="item.extend1"-->
						<div class="bk-r-pic">
							<a href="javascript:;" v-if="item.extend1 !=''">
								<img :src='item.extend1+"?x-oss-process=image/resize,m_fill,h_138,w_160"'/>
							</a>
							<a href="javascript:;" v-else>
								<a href="javascript:;"><img src="../../assets/images/bk_pic3.jpg"></a>
							</a>
						</div>
						<div class="bk-r-xx">
							<h1><a href="javascript:;" @click="kipPage(item.uuid)" v-html="item.title"></a></h1>
							<p>
								<span v-if="item.origin!=''">来源：{{item.origin}}</span>
								<span>发布时间：{{item.release_date | timestampFormat('YYYY-MM-DD')}}</span>
							</p>
							<div class="bk-r-xq">
								<!-- <p v-html="item.tex"></p> -->
								<p>{{item.tex |truncate(170)}}</p>
								<span><a href="javascript:;" @click="kipPage(item.uuid)">查看更多&gt;&gt;</a></span>
							</div>
						
						</div>
					</div>
					
					<!--分页-->
					<div  class="c-page" style="text-align: center; margin-top: 30px;" v-if="classifyconten.length>0">
			       		<el-pagination
					      @current-change="pageFn"
						  layout="prev, pager, next"
						  :total=total>
						</el-pagination>
			       	</div>
				</div>
			</div>
		</div>
	</div>
	
	</section>
	
</template>

<script>
	import {petroleumHome,petroleumList,petroleumLists} from '../../api/info';
	export default {
		data() {
			return {
				total:0,     
				pageNum:1,                 //当前页
				pageSize:10,               //当前页的数量
				topImg:[],                 //头部图片列表
				getClassify:[],            //请求分类列表信息
                classifyconten:[],          //分页列表信息内容    
			  } 
		    },
		methods:{
			
			pageFn(sty){    // 分页
				var objS={
					 "pageNum": sty,
					 "pageSize":this.pageSize 
				}
				petroleumList(objS).then((res)=>{
					this.classifyconten=res.datas.infoList;
					for(var i=0; i<this.classifyconten.length;i++){
						if(this.classifyconten[i].extend1 != ''){
							this.classifyconten[i].extend1=this.classifyconten[i].extend1.split(',')[0];
						}
					}
				})
	        },
			
			//请求分类信息
			getData(){
				petroleumHome().then((res)=>{
			      	this.getClassify=res.datas;
				})
			},
			//获取子集数据请求列表内容
			clickgetClassifyConten(v){
				
				//带参数
				var parameter={};
				var parameter1={
					'stick':'1'
				}
				if(v){
				 	parameter={	
				 		"channelName":v,
				 	}	
				}
				petroleumList(parameter).then((res)=>{
					this.classifyconten=res.datas.infoList;
					this.total=res.total;
					for(var i=0; i<this.classifyconten.length;i++){
						if(this.classifyconten[i].extend1 != ''){
							this.classifyconten[i].extend1=this.classifyconten[i].extend1.split(',')[0];
						}
					}
				})
				petroleumList(parameter1).then((ress)=>{
					this.topImg = ress.datas.infoList
				})
			},
			//跳转详情页面
			kipPage(id){
				window.localStorage.setItem('BKuuid',id)
				this.$router.push({
	 			   path:'/details',
	 			})
			},
			getBanner(){
				queryCarousels({typeCode:"WEB",pageCode:"zx_index_barner",pageNum:1,pageSize:10}).then((res)=>{
					this.imgUrlBanner = res.datas[0].imageUrl
				});
			}
		},
	    created(){
      		this.getData();
      		
      		var channelName = this.$own.getHref('channelName');
      		//有参  
      		if(channelName){
      			this.clickgetClassifyConten(decodeURIComponent(channelName));
      		}else{
      			this.clickgetClassifyConten();
      		}
      		
		},
			mounted(){
				
			}
		}
</script>
<style>


.bk-r-pic{width:160px;height:138px;}
.bk-r-pic a{display: block;}
.bk-r-pic a img{ display: block; margin: 0 auto;}
.bk-img{text-align: center;}
</style>



